import { useState, useCallback, useEffect } from "react";

let timer;
function useAuthHook() {
  const [token, setToken] = useState();
  const [tokenExpireTime, setTokenExpireTime] = useState();
  const [userId, setUserId] = useState(false);

  const login = useCallback((uid, token, expireTime) => {
    setToken(token);
    setUserId(uid);
    const tokenExpireTime =
      expireTime || new Date(new Date().getTime() + 1000 * 60 * 60);
    setTokenExpireTime(tokenExpireTime);
    localStorage.setItem(
      "userData",
      JSON.stringify({
        userId: uid,
        token,
        expiration: tokenExpireTime.toISOString(),
      })
    );
  }, []);

  const logout = useCallback(() => {
    setToken(null);
    setUserId(null);
    setTokenExpireTime(null);
    localStorage.removeItem("userData");
  }, []);

  useEffect(() => {
    if (token && tokenExpireTime) {
      const remainingTime = tokenExpireTime.getTime() - new Date().getTime();
      timer = setTimeout(logout, remainingTime);
    } else {
      clearTimeout(timer);
    }
  }, [tokenExpireTime, token, logout]);

  useEffect(() => {
    const Data = JSON.parse(localStorage.getItem("userData"));
    if (Data && Data.token && new Date(Data.expiration) > new Date()) {
      login(Data.userId, Data.token, new Date(Data.expiration));
    }
  }, [login]);
  return { userId, token, login, logout };
}

export default useAuthHook;
